.app-wrapper {
  padding-left: 200px;
  display: grid;
  
  width: 600px;
  grid-gap: 5px;
  margin-left: 5px;
  margin-right: 5px;
  
  grid-template-areas:
  "h h"
  "n c";

  grid-template-rows: 60px 2fr;
  grid-template-columns: 1fr 6fr;

}

.app-wrapper-content{
  
  grid-area: c;
  border: 3px solid #eee;
}

.wholepage{
  font-style: oblique;
}